@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header-main {
  width: 100vw;
  height: 100vh;
  background-color: #011627;
  position: relative;
  z-index: 5;
}
h1 {
  margin: 0;
}
.about,
.projects,
.contacts {
  color: white;
}
.contenemor-img {
  width: 100%;
  height: 100%;
}
.header-main img {
  width: 47rem;
  height: 99%;
  text-align: right;
  margin-top: 6px;
  position: relative;
  z-index: -1;
}
.lista-Header {
  margin: 0;
  list-style: none;
  height: 100vh;
  color: white;
  font-family: 'Abril Fatface', cursive;
  font-style: bold;
  font-size: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 0rem;
  gap: 0rem;
  position: absolute;
}

.txt2,
.txt3 {
  color: #ffd166;
}

.txt1::before {
  content: 'Hello.';
  transition: 0.4s ease;
  position: absolute;
  opacity: 1;
  cursor: pointer;
}
.txt1:hover::before {
  transform: translateX(50px);
  opacity: 0;
}
.txt1::after {
  content: 'About.';
  transition: 0.4s ease;

  margin-left: 50px;
  z-index: 30rem;
  opacity: 0;
  cursor: pointer;
}

.txt1:hover::after {
  opacity: 1;
}

/* txt2 */
.txt2::before {
  content: 'I am';
  transition: 0.4s ease;
  position: absolute;
  opacity: 1;
  cursor: pointer;
}
.txt2:hover::before {
  transform: translateX(50px);
  opacity: 0;
}
.txt2::after {
  content: 'Projetcs';
  transition: 0.4s ease;
  margin-left: 50px;
  opacity: 0;
  cursor: pointer;
}

.txt2:hover::after {
  opacity: 1;
}

/* txt3 */
.txt3::before {
  content: 'Alexis';
  transition: 0.4s ease;
  position: absolute;
  opacity: 1;
  cursor: pointer;
}
.txt3:hover::before {
  transform: translateX(50px);
  opacity: 0;
}
.txt3::after {
  content: 'Contact';
  transition: 0.4s ease;
  margin-left: 50px;
  opacity: 0;
  cursor: pointer;
}

.txt3:hover::after {
  opacity: 1;
}

