.lista-Header li {
  margin-left: 120px;
}

h1 {
  font-size: 50px;
  text-align: center;
  padding-top: 5rem;
}

.h2 {
  margin: 30px auto;
  width: 50%;
}

.main-about p {
  width: 50%;
  text-align: justify;
  margin: 20px auto;
}
.flotante-volver {
  position: fixed;

  line-height: 55px;
  top: 30px;
  right: 30px;
  color: #fff;
  font-size: 24px;
  z-index: 100;
  list-style: none;
  cursor: pointer;
}

.projects,
.contacts {
  background-color: #011627;
}

.contacts {
  height: 100vh;
}
.flex-redes {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
}
.flex-redes li {
  list-style: none;
}
.card-project {
  width: 70%;
  margin: 0 auto;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  border-left: 2.5px solid #fff;
  border-bottom: 2.5px solid #fff;
  padding: 3rem;
  border-bottom-left-radius: 10px;
}
.card-border {
  border-left: none;
  border-right: 2.5px solid #fff;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 0px;
}
.card-info {
  width: 35%;
  text-align: justify;
}

.card-img img {
  width: 37rem;
  height: 19rem;
  border-radius: 20px;
}

